.rc-slider {
  position: relative;
  padding-top: 7px;
  width: 100%;
  border-radius: 6px;
  box-sizing: border-box;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
.rc-slider * {
  box-sizing: border-box;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
.rc-slider-rail {
  height: 4px;
  margin-top: -2px;
  width: 100%;
  background-color: #e9e9e9;
}
.rc-slider-track {
  height: 4px;
  margin-top: -4px;
  border-radius: 6px;
  background-color: #abe2fb;
}
.rc-slider-handle {
  position: absolute;
  margin-top: -5px;
  margin-left: -7px;
  width: 14px;
  height: 14px;
  cursor: pointer;
  border-radius: 50%;
  border: solid 2px #96dbfa;
  background-color: #fff;
  z-index: 10;
  transform: translate3d(0, 0, 0);
}
.rc-slider-handle:hover {
  border-color: #57c5f7;
}
.rc-slider-handle-active:active {
  border-color: #57c5f7;
  box-shadow: 0 0 5px #57c5f7;
}
.rc-slider-mark-text {
  max-width: 100%;
  margin-top: 0.5em;
  -webkit-hyphens: auto;
  -moz-hyphens: auto;
  cursor: pointer;
  color: #999;
}
.rc-slider-mark-text-active {
  color: #666;
}
.rc-slider-scale {
  position: relative;
  width: 100%;
  background: transparent;
}
.rc-slider-scaleElements {
  margin-top: -2px;
  display: flex;
  justify-content: space-between;
}
.rc-slider-scaleElement {
  margin-top: -4px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.rc-slider-dot {
  width: 8px;
  height: 8px;
  border: 2px solid #e9e9e9;
  background-color: #fff;
  cursor: pointer;
  border-radius: 50%;
  z-index: 1;
}
.rc-slider-dot-active {
  border-color: #96dbfa;
}
.rc-slider-disabled {
  background-color: #e9e9e9;
}
.rc-slider-disabled .rc-slider-track {
  background-color: #ccc;
}
.rc-slider-disabled .rc-slider-handle,
.rc-slider-disabled .rc-slider-dot {
  border-color: #ccc;
  background-color: #fff;
  cursor: not-allowed;
}
.rc-slider-disabled .rc-slider-mark-text,
.rc-slider-disabled .rc-slider-dot {
  cursor: not-allowed !important;
}



